.skill-card {
  background-color: #fff;
  display: flex;
  justify-content: center;
  /* border: 1px solid red; */
  padding: 20px;
  transition: 1s;
  border-radius: 10px;
  border: 3px solid #fff;
  /* border-radius: 50%; */
}
.skill-card:hover {
  transform: scale(1.1);
  /* background-color: rgba(240, 136, 136, 0.119); */
  border: 3px solid crimson;
  box-shadow: rgba(220, 45, 45, 0.523) 0px 8px 15px;
}
@media screen and (min-width: 500px) and (max-width: 1000px) {
  .skill-card {
    width: 100%;
  }
  .skill-card > img {
    /* width: 100; */
    height: auto;
  }
}
@media screen and (max-width: 500px) {
  .skill-card {
    width: 100%;
  }
  .skill-card > img {
    /* width: 100px; */
    height: auto;
  }
}
