#github_section {
  overflow: hidden;
  background-color: #edf2f8;
}
#github_section > div {
  width: 70%;
  margin: 30px auto;
  /* border: 1px solid red; */
  padding: 40px 0;
}
.stats_container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  flex-wrap: wrap;
}
.stats_container > img {
  /* max-width: 100%; */
  width: 80%;
  margin: 10px auto;
}
@media screen and (max-width: 1000px) {
  #github_section > div {
    width: 90%;
    margin: auto;
  }
}
@media screen and (max-width: 500px) {
  #github_section > div {
    width: 95%;
    margin: auto;
  }
}
